
.bmix-section-padding{
    padding: 130px 0 56px;
    @include max(991px) {
        padding: 100px 0 56px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}
.bmix-section-padding2 {
    padding: 80px 0 130px;
    @include max(991px) {
        padding: 80px 0;
    }
}
.bmix-section-padding3 {
    padding: 0 0 130px;
    @include max(991px) {
        padding: 0 0 90px;
    }
    @include max(767px) {
        padding: 0 0 60px;
    }
    @include max(425px) {
        padding: 0 0 40px;
    }
}

.bmix-section-title {
    p{
        font-size: 20px;
    }
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-630 {
            max-width: 630px;
        }
        &.max-width-700 {
            max-width: 700px;
        }
        &.max-width-750 {
            max-width: 750px;
        }
    }
}



.bmix-title-btn {
    @include max(991px) {
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    
}

.bmix-extra-mt {
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}

.bmix-default-content {
    h2 {
        margin-bottom: 20px;
    }
    @include max (991px){
        text-align: center;
    }

}







@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
  