.medicx-blog{
    &-wrap {
        border: 1px solid var(--light-color);
        border-radius: 12px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000012!important;
            border: 1px solid transparent;
            .medicx-blog-img  img {
                transform: scale(1.1);            }
        }
        &:hover{
            .medicx-blog-metav1v3{
                border-radius: 0 0 0 0!important;
            }
        }
    }
    &-img{
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
        }
    }
    &-content{
        padding: 30px 17px;
        h5{
            margin-bottom: 30px;
        }
        h4{
            margin-bottom: 24px;
        }
    }
  
    &-metav1 {
        margin-bottom: 24px;
        ul{
            li{
                display: inline-block;
                font-size: 18px;
                line-height: 28px;
                color: var(--heading-color);
                position: relative;
                padding-left: 30px;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 4px;
                }
            }
        }
    }
    &-metav1v3 {
        margin-top: -40px;
        z-index: 1;
        padding: 6px 18px;
        position: absolute;
        transition: all 0.4s;
        border-radius: 10px 10px 0 0;
        background: var(--light-color2);
        ul{
            li{
                font-weight: 700;
                font-size: 16px;
                line-height: 28px;
                color: var(--heading-color);
            }
        }
    }
    &-btn-icon{
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            margin-left: 10px;
        }
        &:hover{
            color: var(--accent-color)!important;
            svg path{
                stroke: var(--accent-color);
            }
        }
    }
    &-btn-icon2{
       
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            margin-left: 10px;
            width: 30px;
            height: 30px;
            padding: 0px 8px;
            background-color: var(--accent-color);
            border-radius: 100px;
        }
        &:hover{
            color: var(--heading-color)!important;
        }
    }
    // blog-page
    &-wrap2{
        border-radius: 10px;
        margin-bottom: 40px;
        transition: all 0.4s;
        border: 1px solid var(--light-color2);
        &:hover{
            border-radius: 10px;
            border: 1px solid var(--white-color);
            box-shadow: 0px 4px 80px 0px #00000012;
        }
        &:hover .medicx-blog-thumb img {
            transform: scale(1.1);
        }
    }
    &-thumb{
        overflow: hidden;
        img{
            transition: all .4s;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &-content2{
        padding: 30px 40px 40px 30px;
        h4{
            margin-bottom: 20px;
        }
        p{
            font-size: 20px;
            margin-bottom: 40px;
        }
    }
    &-sidebar{
        padding-left: 20px;
        @include max (991px){
            padding-left: 0;
            margin-top: 40px;
        }
    }
    &-widgets{
        border-radius: 10px;
        margin-bottom: 30px;
        padding: 30px;
        border: 1px solid var(--light-color2);
        h5{
            margin-bottom: 24px;
        }
        .search-icon{
            position: absolute;
            top: 0;
            right: 0;
            padding: 15px 18px;
            background: var(--heading-color);
        }
        .medicx-recent-post-item {
            gap: 20px;
            display: grid;
            align-items: center;
            overflow: hidden;
            margin-bottom: 24px;
            grid-template-columns: 96px 1fr;
            &:last-child{
                margin-bottom: 0;
            }
        }
        .medicx-recent-post-data{
            span{
                font-size: 18px;
                color: var(--body-color);
                img{
                    margin-bottom: 5px;
                    margin-right: 10px;
                }
            }
        }
        .medicx-blog-tags{
            ul {
                li{
                    display: inline-block;
                    margin-right: 5px;
                    margin-bottom: 24px;
                    &:nth-child(4){
                        margin-bottom: 0;
                    }
                    &:nth-child(5){
                        margin-bottom: 0;
                    }
                    @include max (991px){
                        margin-right: 15px;
                    }
                    @include max (425px){
                        margin-right: 8px;
                    }
                    a{
                        font-size: 16px;
                        color: var(--heading-color);
                        padding: 8px 10px;
                        border-radius: 50px;
                        transition: all 0.4s;
                        border: 1px solid var(--heading-color);
                        &:hover{
                            color: var(--white-color);
                            border: 1px solid transparent;
                            background: var(--accent-color);
                        }
                    }
                }
            }
        }
    }
    &-categorie{
        ul{
            li{
                padding-bottom: 15px;
                border-bottom: 1px solid rgba(3, 3, 3, 0.1);
                margin-bottom: 15px;
                &:last-child{
                    padding-bottom: 0;
                    border-bottom: none;
                    margin-bottom: 0;
                }
                a{
                    display: flex;
                    font-size: 18px;
                    transition: all .4s;
                    justify-content: space-between;
                    color: var(--heading-color);
                }
            }
        }
    }
}

.mb-20{
    margin-bottom: 20px;
}

.medicx-post-navigation-wrap{
    display: flex;
    gap: 24px;
    max-width: 300px;
    .title-post-content{
        ul{
            li{
                font-size: 15px!important;
                line-height: 16px;
                font-weight: 400;
                margin-bottom: 10px;
            }
        }
    }
}
.nav-preview{
    &-wrap{
        @include max(991px){
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        
    }
    &-icon{
        margin-bottom: 24px;
        a{
            color: var(--dark-bg);
            font-size: 13px;
            font-weight: 700;
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}
.medicx-post-navigation-wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--light-color);
    margin-bottom: 40px;
}


.medicx-blog{
    &-thumb{
        img{
            width: 100%;
        }
    }
    &-thumb2{
        margin-bottom: 30px;
    }
}
.medicx-single-post-content-wrap{
    .medicx-blog-entry{
        &-content{
            p{
                font-size: 20px;
            }
            .medicx-blog-quote-content{
                &-wrap{
                    padding: 30px;
                    margin-top: 30px;
                    margin-bottom: 24px;
                    border-radius: 10px;
                    background: rgba(0, 34, 255, 0.05);
                }
                &-icon{
                    margin-bottom: 24px;
                }
                &-text{
                    margin-bottom: 24px;
                }
                &-authore{
                    h6{
                        font-weight: 600;
                        margin-bottom: 5px;
                    }
                }
                &-text2{
                    h6{
                        margin-bottom: 24px!important;
                    }
                    ul{
                        margin-top: 30px;
                        li{
                            font-weight: 600;
                            margin-bottom: 15px;
                            &:last-child{
                                margin-bottom: 0;
                            }
                        }
                    }
                    span{
                        font-weight: 400;
                        font-size: 20px;
                        line-height: 32px;
                        color: var(--body-color);
                    }
                }
            }
        }
    }
    .medicx-post-navigation{
        display: flex;
        align-items: center;
        padding-top: 24px;
        border-top: 1px solid rgba(0, 34, 255, 0.1);
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(0, 34, 255, 0.1);
        margin-bottom: 40px;
        justify-content: space-between;
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
        .medicx-nav-previous{
            a.nav-previous {
                display: flex;
                align-items: center;
                gap: 10px;
                margin-bottom: 18px;
                
                .medicx-previous-icon{
                    img{
                        margin-bottom: 5px;
                    }
                }
                
            }
            .nav-previous-content{
                p{
                    max-width: 320px;
                }
            }
            &.tae{
                text-align: end;
                a.jce {
                    justify-content: end;
                }
            }
        }
        
       
    }
}


.medicx-blog-tag{
    &-wrapper {
        display: flex;
        justify-content: space-between;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(0, 34, 255, 0.1);
        margin-bottom: 40px;
        @include max (991px){
            display: block;
        }
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    }
    &-wrap{
        display: flex;
        gap: 5px;
        
        @include max (991px){
            h5{
                margin-bottom: 20px;
            }
        }
        ul{
            li{
                margin-left: 10px;
                display: inline-block;
                margin-bottom: 15px;
                @include max (991px){
                    margin-left: 30px;
                }
            }
        }
    }
}

.medicx-post-comment{
    h5{
        margin-bottom: 24px;
    }
    ul{
        li{
            
            &.medicx-post-comment-wrap {
                display: flex;
                gap: 15px;
                &.children{
                    padding-top: 24px;
                    border-top: 1px solid rgba(0, 34, 255, 0.1);
                    margin-top: 24px;
                    padding-bottom: 24px;
                    border-bottom: 1px solid rgba(0, 34, 255, 0.1);
                    margin-bottom: 24px;
                    margin-left: 85px;
                    
                }
                .medicx-post-comment{
                    &-data{
                        position: relative;
                        max-width: 675px;
                        @include max (1199px){
                            max-width: 470px;
                        }
                        @include max (767px){
                            max-width: 330px;
                        }
                        .medicx-comment-reply{
                            position: absolute;
                            bottom: 85px;
                            right: -60px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                max-width: 470px;
                                bottom: 115px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 180px;
                                right: -65px;
                            }
                            @include max (425px){
                                right: 30px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    &-data2{
                        position: relative;
                        max-width: 593px;
                        @include max (1199px){
                            max-width: 418px;
                        }
                        @include max (767px){
                            max-width: 300px;
                        }
                        .medicx-comment-reply2{
                            position: absolute;
                            bottom: 85px;
                            right: -85px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                bottom: 120px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 150px;
                                right: -50px;
                            }
                            @include max (425px){
                                bottom: 180px;
                                right: 24px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    
                }
                h6{
                    margin-bottom: 5px;
                }
                p{
                    margin-top: 15px;
                }
            }
            &.medicx-post-comment-wrap2{
                padding-bottom: 50px;
                border-bottom: 1px solid rgba(0, 34, 255, 0.1);
                margin-bottom: 50px;
            }
        }
    }
}

