input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 400;
  border-radius: 50px;
  padding: 15px 24px 15px 24px ;
  border: none!important;
  transition: all 0.4s;
  border: 1px solid var(--light-color2)!important;
  
&:focus-visible{
  outline: var(--light-color2);
}

}
input:focus, 
textarea:focus{
  border: 1px solid var(--light-color2)!important;
}
textarea{
  height: 200px;
  border-radius: 15px;
  @include max (767px){
    height: 100px;
  }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--body-color);
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--body-color);
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--body-color);
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--body-color);
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--body-color);
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--body-color);
   opacity: 1;
}

// end default form
// bmix project


button#bmix-subscription-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 13px 22px;
  border-radius: 50px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  width: 140px;
  height: 50px;
  color: var(--white-color);
  background-color: var(--accent-color);
  transition: all .4s;
  &:hover{
    background: var(--heading-color);
  }
}

#bmix-subscription-btn {
  @media (max-width: 767px) {
      position: static!important;
      width: 100%!important;
      margin-top: 24px;
  }
}