.bmix-hero-thumb{
    @include max (991px){
        margin-bottom: 40px;
    }
}
.bmix-about{
    &-thumb{
        position: relative;
    }
    &-card{
        position: absolute;
        top: 150px;
        right: 70px;
        overflow: hidden;
        transform: translatey(0px);
        -webkit-animation: updown 3s ease-in-out infinite;
        animation: updown 3s ease-in-out infinite;
    }
}