.service-section{
    h2{
        color: var(--white-color);
    }
}



.servicev1{
    margin-top: -100px;
    @include max (991px){
        margin-top: -80px;
    }
    @include max (767px){
        margin-top: -60px;
    }
}
.medicx-service-slider{
    &-wrap{
        display: flex!important;
        gap: 30px;
        align-items: center;
        margin-right: 24px;
    }
    &-icon {
        display: flex;
        flex-shrink: 0;
        align-items: center;
        animation: animName 3s linear infinite;
    }
    &-data{
        flex-shrink: 0;
        margin-right: 60px;
        h4{
            white-space: nowrap;
            letter-spacing: 0.005em;
            color: var(--white-color);
            text-transform: uppercase;
        }
    }
}
.medicx-slider-service-section{
    .slick-slide{
        width: auto!important;
    }
    
    .slick-track{
        display: flex;
    }
}

.medicx-content-slider{
    background: var(--accent-color);
    padding: 30px 0;
}
// v2




.medicx-service{
    &-wrap{
        display: block;
        position: relative;
        .service_position {
            transition: all .4s;
            position: absolute;
            bottom: 0;
            left: 24px;
            width: 100%;
            opacity: 0;
            visibility: hidden;
            width: 415px;
            padding: 24px;
            border-radius: 10px;
            background-color: var(--accent-color);
            h4{
                margin-bottom: 15px;
            }
            @include max(767px) {
                position: inherit;
                opacity: 1;
                visibility: visible;
                width: 100%;
                left: 0;
            }
        }
        :hover .medicx-service-thumb::before {
            opacity: 1;
        }
        :hover .service_position {
            opacity: 1;
            visibility: visible;
            bottom: 24px;
            left: 24px;
        }
    }
    &-thumb{
        margin-bottom: 24px;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: .3s;
        }
    }
    &-content-wrap{
        h4{
            color: var(--white-color);
        }
        p{
            font-size: 18px;
            color: var(--white-color);
        }
        a.medicx-site-btn{
            font-size: 16px;
            line-height: 28px;
            font-weight: 700;
            color: var(--white-color);
            img{
                margin-left: 10px;
            }
        }
    }
    
}
.mt-60{
    margin-top: 60px;
    @include max (991px){
        margin-top: 40px;
    }
}


.medicx-service-details{
    &-text{
        ul{
            li{
                font-weight: 600;
            }
        }
        p {
            span{
                border-bottom: 2px solid var(--light-color);
            }
        }
        ul{
            li{
                span{
                    border-bottom: 2px solid var(--light-color);
                }
            }
        }
        &.bl{
            padding-left: 10px;
            border-left: 5px solid var(--accent-color);
        }
    }
    &-skill{
        h4{
            margin-bottom: 15px;
        }
        p{
            font-size: 18px;
            margin-bottom: 24px;
        }
        ul{
            li{
                font-weight: 600;
                margin-bottom: 5px;
                color: var(--heading-color);
            }
        }
    }
}
