.medicx-portfolio-content{
  &-wrap{
    position: relative;
    display: block;
    margin-bottom: 24px;
    padding: 0 15px;
    &:hover .medicx-portfolio-thumb img{
      transform: scale(1.1);
    }
    .medicx-portfolio-thumb{
      border-style: solid;
      border-width: 3px 3px 3px 3px;
      border-color: #0E0E0E;
      border-radius: 16px;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all .8s;
      }
    }
    .medicx-portfolio-thumb2{
      position: relative;
      img{
        border-radius: 10px;
        
      }
      &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        transition: all 0.4s;
        border-radius: 10px;
        background: linear-gradient(180deg, rgba(3, 3, 3, 0) 0%, #030303 100%);
      }
    }
    &:hover .medicx-portfolio-thumb2::before {
      opacity: 1;
    }
    &:hover .social_position {
      opacity: 1;
      visibility: visible;
      bottom: 10px;
    }
    .social_position {
      transition: all .4s;
      position: absolute;
      bottom: 0;
      text-align: center;
      width: 100%;
      opacity: 0;
      visibility: hidden;
    }
  }
  &-icon {
    position: absolute;
    bottom: 200px;
    left: 40%;
  }
  &-data {
    position: absolute;
    bottom: 0;
    left: 40px;
    h6{
      width: 90px;
      padding: 5px 15px;
      background: var(--white-color);
      border: transparent;
      border-radius: 50px;
      text-align: left;
      margin-bottom: 10px;
      font-size: 12px;
      line-height: 20px;
    }
  }
}


.medicx-portfolio{
  &-menu{
    @include max (1199px){
      text-align: center;
    }
    ul{
      margin-bottom: 80px;
      @include max (991px){
        margin-bottom: 60px;
      }
      @include max (767px){
        margin-bottom: 40px;
      }
      li{
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        margin-right: 24px;
        padding: 16px 53px;
        border-radius: 100px;
        display: inline-block;
        color: var(--heading-color);
        border: 2px solid var(--heading-color);
        &:last-child{
          margin-right: 0;
        }
        @include max (1400px){
          margin-bottom: 24px;
        }
        &.active{
          background: var(--accent-color);
          color: var(--white-color);
          border: 1px solid var(--accent-color);
      }
      }
    }
  }
}

// portfolio details

.medicx-pd-thumb{
  padding-bottom: 24px;
  img{
    width: 100%;
  }
}
.medicx-portfolio-info{
  display: flex;
  justify-content: space-between;
  &-item{
    display: flex;
    align-items: center;
    gap: 5px;
    span{
      color: var(--body-color);
    }
    @include max (1199px){
      display: block;
    }
  }
}
.medicx-pd-content{
  &-wrap{
    max-width: 975px;
    @include max (991px){
      margin-top: 40px;
    }
    p{
      margin-bottom: 24px;
    }
    ul{
      li{
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 24px;
        color: var(--heading-color);
        span{
          font-weight: 400;
          color: var(--body-color);
        }
      }
    }
  }
  &-thumb {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &-data{
    padding-left: 8px;
    border-left: 5px solid var(--accent-color);
  }
}
.medicx-portfolio-details{
  &-text{
      p{
          font-size: 20px;
          line-height: 32px;
      }
      ul{
          p{
              margin-top: 40px;
              @include max (767px){
                  margin-top: 30px;
              }
          }
          li{
              font-weight: 600;
              margin-bottom: 8px;
          }
      }
      
  }
  &-skill{
      h4{
          margin-bottom: 10px;
      }
      p{
          margin-bottom: 24px;
      }
      ul{
          li{
              font-weight: 600;
              font-size: 20px;
              color: var(--heading-color);
              margin-bottom: 12px;
              &:last-child{
                  margin-bottom: 0;
              }
          }
      }
  }
}