.medicx-faq-content{
    
    &-title{
        
        ul{
            &.nav-tabs {
                display: flex;
                justify-content: center;
                border: none;
                padding-bottom: 80px;
                @include max (991px){
                    padding-bottom: 60px;
                }
                @include max (767px){
                    padding-bottom: 40px;
                }
            }
            li{
                &.nav-item{
                @include max (1399px){
                    margin-bottom: 20px;                    }
                }
                &:last-child{
                    margin-bottom: 0;
                  }
                a{
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 28px;
                    padding: 14px 40px;
                    border-radius: 50px;
                    margin-right: 30px;
                    color: var(--heading-color)!important;
                    border: 2px solid var(--heading-color)!important;
                    
                    &.nav-link.active{
                        background: var(--accent-color);
                        border: 2px solid transparent!important;
                        color: var(--white-color)!important;
                    }
                    @include max (991px){
                        margin-right: 24px;
                    }
                }
            }
        }
    }
}
.medicx-faq-content-title .nav-tabs .nav-link {
    
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}
.medicx-faq-contact{
    &-infobox {
        text-align: center;
        padding: 50px;
        position: relative;
        z-index: 0;
        &::before{
            content: "";
            border-radius: 5px;
            width: 100%;
            height: 77%;
            position: absolute;
            z-index: -1;
            bottom: -10px;
            left: 0;
            border-radius: 10px;
            background-color: rgba(0, 34, 255, 0.05);
        }
    }
    &-icon{
        margin-bottom: 24px;
    }
    &-data{
        padding: 0 10px;
        h4{
            font-size: 35px;
            line-height: 42px;
            padding-bottom: 24px;
            border-bottom: 1px solid var(--light-color);
            margin-bottom: 15px;
        }
        h6{
            color: var(--accent-color);
            margin-bottom: 5px;
        }
    }
}