.medicx-about{
    &-wrapperv3 {
        padding: 40px;
        background: white;
        border-radius: 10px;
        display: block;
        position: relative;
        margin-bottom: 24px;
        .medicx-about-btn{
            svg{
                margin: 8px 0 0 10px;
            }
            &.about-position {
                transition: all .4s;
                position: absolute;
                bottom: 125px;
                left: 0;
                text-align: center;
                width: 100%;
                opacity: 0;
                visibility: hidden;
            }
        }
        &:hover .medicx-about-thumb::before {
            opacity: 1;
        }
        &:hover .about-position {
            opacity: 1;
            visibility: visible;
            bottom: 30%;
            left: 0%;
        }
    }
    &-titlev3{
        &-wrap{
            display: flex;
            gap: 10px;
            align-items: center;
            margin-bottom: 24px;
        }
    }
    &-thumb{
        position: relative;
        img{
            border-radius: 10px;
            width: 100%;
        }
        p{
            font-size: 20px!important;
        }
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(3, 3, 3, 0) 0%, #030303 100%);
            opacity: 0;
            transition: .3s;
        }
    }
    &-data{
        &.aboutv3{
            p{
                font-size: 20px!important;
                line-height: 32px!important;
                color: var(--heading-color);
            }
        }
        p{
            margin-top: 24px;
        }
    }
}
// aboutus inner page

.medicx-aboutus-content{
    margin-top: 80px;
    @include max (991px){
        margin-top: 60px;
    }
    @include max (767px){
        margin-top: 40px;
    }
    &-title{
        margin-top: 35px;
        @include max (767px){
            margin-top: 50px;
        }
        ul{
            &.nav-tabs {
                width: 850px;
                height: 6px;
                border-radius: 100px;
                display: flex;
                justify-content: space-between;
                border-bottom: 5px solid rgba(0, 34, 255, 0.05);
                margin: 0 auto;
                @include max (991px){
                    width: 100%;
                    justify-content: space-between;
                }
                @include max (767px){
                    justify-content: center;
                    flex-wrap: nowrap;
                }
            }
            li{
                &.nav-item{
                    display: flex;
                    margin-top: -42px;
                    
                    @include max (767px){
                        margin-top: -62px;
                    }  
                }
            }
            a{
                &.nav-link {
                    margin-bottom: -1px;
                    background: 0 0;
                    border: none;
                    border-top-left-radius: .25rem;
                    border-top-right-radius: .25rem;
                    padding: 0;
                    h6{
                        @include max (575px){
                            font-size: 18px;
                        }
                    }

                }
                &.nav-link.active{
                    margin-bottom: -5px;
                    border-bottom: 5px solid var(--accent-color);
                }
            }
        }
    }
}
