
//  header social

.bmix-social-icon{
    &-wrap{
      @include max (1199px){
        margin-top: 15px;
      }
    }
    &-img{
        ul {
            li{
                margin-left: 24px;
                display: inline-block;
                @include max (767px){
                  margin-bottom: 20px;
                }
                a{
                    width: 30px;
                    height: 30px;
                    border-radius: 100px;
                    border: 1px solid var(--white-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .4s;
                    margin-left: 24px;
                    &:last-child{
                        margin-left: 0;
                    }
                    &:hover{
                        background: var(--accent-color);
                        border: transparent;
                    }
                }
            }
        }
    }
    // end 

    &-wrap2{
      @include max (1199px){
        margin-top: 15px;
      }
    }
    &-img2{
        ul {
            li{
                margin-left: 24px;
                display: inline-block;
                @include max (767px){
                  margin-bottom: 20px;
                }
                a{
                    width: 30px;
                    height: 30px;
                    border-radius: 100px;
                    border: 1px solid var(--white-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .4s;
                    margin-left: 24px;
                    &:last-child{
                        margin-left: 0;
                    }
                    &:hover{
                      border: transparent;
                      background: var(--white-color);
                    }
                    &:hover svg path{
                      fill: var(--accent-color);
                    }
                }
            }
        }
    }
}
.bmix-footer-social{
  &-icon{
    margin-top: 30px;
    ul {
      li{
        margin-right: 15px;
        display: inline-block;
        &:last-child{
          margin-right: 0;
      }
        @include max (767px){
          margin-bottom: 20px;
        }
        a{
          width: 40px;
          height: 40px;
          border-radius: 100px;
          border: 1px solid var(--heading-color);
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all .4s;
          svg path {
            transition: all 0.4s;
          }
          &:hover{
            border: transparent;
            background: var(--accent-color);
            svg path{
            fill: var(--white-color);
            border: 1px solid var(--accent-color);
            }
          }
        }
      }
    }
  }
}