//  medicx pricing section 

.medicx-pricing{
    &-wrapper {
      display: flex;
      align-items: center;
      @include max (991px){
        display: block;
      }
    }
    &-wrap{
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        border-radius: 20px 0 0 20px;
        background-color:  var(--white-bg);
        box-shadow: 0px 4px 80px 0px #00000014;
        @include max (1399px){
          height: 100%;
        }
        @include max (991px){
          border-radius: 20px;
      }
        &.active{
            width: 555px;
            padding: 50px;
            border-radius: 20px;
            position: relative;
            background: var(--heading-color);
            @include max (1399px){
                height: 100%;
            }
            @include max (991px){
              width: 100%;
            }
            .medicx-pricing-shape {
              position: absolute;
              top: 0;
              right: 0;
            @include max (1199px){
              display: none;
            }
            @include max (991px){
              display: block;
            }
          }
          .medicx-pricing{
            &-header{
              h4{
                color: var(--white-color);
              }
            }
            &-price{
              h2,p{
                color: var(--white-color);
              }
            }
            &-body{
              ul{
                li{
                  font-size: 24px;
                  line-height: 36px;
                  color: var(--white-color);
                  @include max (575px){
                    font-size: 20px;
                    line-height: 28px;
                  }
                  img{
                    position: absolute;
                    top: 10px;
                    left: 0;
                  }
                }
              }
            }
          }
          }
        &.border-radius{
            border-radius: 0px 20px 20px 0px;
        }
    }
    &-header{
      margin-bottom: 15px;
      img{
          margin-bottom: 30px;
      }
    }
    &-price{
      display: flex;
      align-items: flex-end;
      padding-bottom: 24px;
      margin-bottom: 24px;
      border-bottom: 1px solid var( --light-color2);
      .medicx-price-value{
        font-weight: 700;
        color: var(--heading-color);
        font-size: 70px;
        line-height: 80px;
      }
    }
    &-body{
      ul{
          li{
              position: relative;
              font-weight: 700;
              padding-left: 38px;
              margin-bottom: 15px;
              color: var(--heading-color);
              &:last-child{
                  margin-bottom: 0;
              }
              img{
                  position: absolute;
                  left: 0;
                  top: 5px;
              }
          }
      }
    }
    &-bodyv4 {
      margin-bottom: 0!important;
    }
  }
  