.bmix-counter{
    &-wrap{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        text-align: center;
    }
    &-number{
        font-size: 100px;
        line-height: 1.07em; /* 116.667% */
        letter-spacing: -0.005em;
        font-weight: 700;
        color: var(--white-color);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
            font-weight: 600;
        }
    }
}